.slider {
  position: relative;
  width: 450px;
  height: 450px;
  margin: auto;
  overflow: visible;
}

.slide {
  position: absolute;
  width: 450px;
  height: 450px;
  position: absolute;
  transition: 0.6s ease;
  -webkit-transform: translate(-100%, 0);
          transform: translate(-100%, 0);
}

.slide.active {
  width: 600px;
  height: 600px;
  top: -30px;
  left: -70px;
  -webkit-transform: translate(0, 0);
          transform: translate(0, 0);
  z-index: 100;
  opacity: 100%;
  -webkit-animation: 1s ease-out shake forwards;
          animation: 1s ease-out shake forwards;
  cursor: pointer;
}



.slide.last {
  -webkit-transform: translate(100%, 0);
          transform: translate(100%, 0);
}




@-webkit-keyframes shake {
 
  
  0%,10%,20%,30%,40%,50% {   
    -webkit-transform: rotate(0deg);   
            transform: rotate(0deg);
  }
  5%,25%
  
   {
     -webkit-transform: rotate(1deg);
             transform: rotate(1deg);
  }

  15%,
  35%
   {
    -webkit-transform: rotate(-1deg);
            transform: rotate(-1deg);
  }

  60% {
   
    -webkit-transform: rotate(0deg);
   
            transform: rotate(0deg);
  }
}




@keyframes shake {
 
  
  0%,10%,20%,30%,40%,50% {   
    -webkit-transform: rotate(0deg);   
            transform: rotate(0deg);
  }
  5%,25%
  
   {
     -webkit-transform: rotate(1deg);
             transform: rotate(1deg);
  }

  15%,
  35%
   {
    -webkit-transform: rotate(-1deg);
            transform: rotate(-1deg);
  }

  60% {
   
    -webkit-transform: rotate(0deg);
   
            transform: rotate(0deg);
  }
}


